.team-swiper {
  padding-top: 8.3rem;
  padding-bottom: 8.3rem;
}

.team-swiper__main-text-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.team-swiper__main-title {}

.team-swiper__link-page {
  // font-weight: 700;
  // font-size: 2.2rem;
  // text-decoration: underline;
}

.team-swiper__big-container {
  .swiper-container {}
}

.team-swiper__swiper-container {
  padding-top: 9.8rem;
}

.team-swiper__slide {}

.team-swiper__item {
  box-sizing: border-box;
  position: relative;

  width: 30.6rem;
  padding-top: 3.2rem;
  padding-left: 7.3rem;
  padding-right: 2rem;
  padding-bottom: 2.6rem;

  background-color: #FFF;
  border-radius: 7rem;
  box-shadow: 0px 20px 30px #D4E2F1;

  transition: @s;

  &:hover,
  &:focus {
    box-shadow: none;
    .team-swiper__author-photo {
      // box-shadow: none;
    }
  }
}

.team-swiper__author-photo-wrap {
  position: absolute;
  top: -3.1rem;
  left: 1.9rem;

  width: 6.6rem;
  height: 6.6rem;
}

.team-swiper__author-photo {
  .img-cover;
  border-radius: 50%;
  box-shadow: 2px 25px 35px #BAC2CE;
  transition: @s;
}

.team-swiper__author-name {
  display: inline-block;

  font-style: normal;
  font-weight: 700;
  font-size: 2rem;
}

.team-swiper__author-theme {
  margin-bottom: 0.3rem;
  font-size: 1.6rem;
  color: @light-gray;
}

.team-swiper__arrows-wrap {}

.team-swiper__button-prev {}

.team-swiper__button-next {}

.team-swiper__swiper-pagination {}

@media (max-width: @tablet-width) {
  .team-swiper {
    padding-top: 9.4rem;
    padding-bottom: 9.4rem;
  }

  .team-swiper__main-text-wrap {
    flex-direction: column;
    align-items: flex-start;
  }

  .team-swiper__main-title {
    margin-bottom: 1.6rem;
  }

  .team-swiper__link-page {
    // font-size: 1.5rem;
  }

  .team-swiper__swiper-container {
    padding-top: 7.3rem;
  }

  .team-swiper__item {
    width: auto;

    padding-top: 4.7rem;
    padding-left: 2.3rem;
    padding-right: 1.8rem;
    padding-bottom: 2.5rem;

    background-image: linear-gradient(216.95deg, #FFFFFF 22.57%, #E6F1FD 184.71%);
    box-shadow: 5px 25px 35px #E4E9EF;
    border-radius: 5rem;
  }

  .team-swiper__author-photo-wrap {
    top: -3.1rem;
    left: 2.2rem;

    width: 6.6rem;
    height: 6.6rem;
  }

  .team-swiper__author-theme {
    margin-bottom: 0.7rem;
    font-size: 1.4rem;
    line-height: 1.6rem;
  }

  .team-swiper__author-name {
    font-size: 1.7rem;
    line-height: 1.1;
  }
}
